<template>
  <div>
    <p>Print Diff Lines</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <div>
      <textarea v-model="aa_text" id="textarea_aa" rows="13" cols="30" placeholder="aa textarea , please input some string on diff lines"></textarea>
      <textarea v-model="bb_text" id="textarea_bb" rows="13" cols="30" placeholder="bb textarea , please input some string on diff lines"></textarea>
    </div>
    <div>
      <textarea v-model="share_text" id="textarea_share" rows="13" cols="30" placeholder="share lines" readonly></textarea>
      <textarea v-model="only_aa_text" id="textarea_only_aa" rows="13" cols="30" placeholder="only in aa textarea" readonly></textarea>
      <textarea v-model="only_bb_text" id="textarea_only_bb" rows="13" cols="30" placeholder="only in bb textarea" readonly></textarea>
    </div>
    <div>
      <input value="print diff" type="button" @click="print_diff(false)">
      <input value="print diff with count" type="button" @click="print_diff(true)">
      <input value="clean data" type="button" @click="clean()">
      <input value="trim data" type="button" @click="trim()">
    </div>
  </div>
</template>

<script>

export default {
  name: 'DiffLinesView',
  data () {
    return {
      aa_text: '',
      bb_text: '',
      share_text: '',
      only_aa_text: '',
      only_bb_text: ''
    }
  },
  methods: {
    clean () {
      this.aa_text = ''
      this.bb_text = ''
      this.share_text = ''
      this.only_aa_text = ''
      this.only_bb_text = ''
    },
    print_diff (count) {
      // clean history
      this.only_aa_text = ''
      this.only_bb_text = ''
      this.share_text = ''
      let aaArr = this.aa_text.split('\n')
      let bbArr = this.bb_text.split('\n')
      const aaMap = new Map()
      const bbMap = new Map()
      aaArr.forEach(element => {
        aaMap.set(element, aaMap.get(element) === undefined ? 1 : aaMap.get(element) + 1)
      })
      bbArr.forEach(element => {
        bbMap.set(element, bbMap.get(element) === undefined ? 1 : bbMap.get(element) + 1)
      })
      aaArr = []
      bbArr = []
      if (count) {
        aaMap.forEach((v, k) => {
          aaArr.push(k + '&' + v)
        })
        bbMap.forEach((v, k) => {
          bbArr.push(k + '&' + v)
        })
      } else {
        aaMap.forEach((v, k) => {
          aaArr.push(k)
        })
        bbMap.forEach((v, k) => {
          bbArr.push(k)
        })
      }
      // only in aa
      let aaOnly = ''
      aaArr.filter((i) => !bbArr.includes(i)).forEach((i) => {
        aaOnly = aaOnly + i + '\n'
      })
      // only in bb
      let bbOnly = ''
      bbArr.filter((i) => !aaArr.includes(i)).forEach((i) => {
        bbOnly = bbOnly + i + '\n'
      })
      // share
      let share = ''
      aaArr.filter((i) => bbArr.includes(i)).forEach((i) => {
        share = share + i + '\n'
      })
      this.only_aa_text = aaOnly
      this.only_bb_text = bbOnly
      this.share_text = share
    },
    trim () {
      const aaArr = this.aa_text.split('\n')
      const bbArr = this.bb_text.split('\n')
      let aaStrTarget = ''
      let bbStrTarget = ''
      aaArr.forEach(e => {
        aaStrTarget += e.trim() + '\n'
      })
      bbArr.forEach(e => {
        bbStrTarget += e.trim() + '\n'
      })
      this.aa_text = aaStrTarget
      this.bb_text = bbStrTarget
    }
  }
}
</script>
